.weather {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.day {
  font-size: 1.5rem;
  margin-bottom: 1rem;
  font-weight: 200;
}

.temperature {
  font-size: 1.5rem;
  font-weight: 300;
}

.icon {
  width: 70px;
  margin: 1rem 0;
}

.description {
  font-size: 1rem;
  color: rgba(0, 0, 0, 0.5);
}